import React, {useState} from 'react';
import {
    TextField,
    Button,
    Typography,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    Box
} from '@mui/material';

const finalExamRanks = {
    "1": {rating: 1700, idolParameterIncrease: 30},
    "2": {rating: 900, idolParameterIncrease: 20},
    "3": {rating: 500, idolParameterIncrease: 10},
};

const IdolRanks = [
    {name: "SS", necessaryRating: 16000},
    {name: "S+", necessaryRating: 14500},
    {name: "S", necessaryRating: 13000},
    {name: "A+", necessaryRating: 11500},
    {name: "A", necessaryRating: 10000},
];

const finalExamScoreRates = [
    {upperBound: 5000, rate: 30},
    {upperBound: 10000, rate: 15},
    {upperBound: 20000, rate: 8},
    {upperBound: 30000, rate: 4},
    {upperBound: 40000, rate: 2},
    {upperBound: 100000000, rate: 1},
];

const calculateNecessaryFinalExamScoreForSpecificRating = (rating) => {
    let restRating = rating;
    for (const [index, finalExamScoreRate] of finalExamScoreRates.entries()) {
        const beforeUpperBound = index === 0 ? 0 : finalExamScoreRates[index - 1].upperBound;
        const maxRatingInThisRange = ((finalExamScoreRate.upperBound - beforeUpperBound) * finalExamScoreRate.rate) / 100;
        if (restRating > maxRatingInThisRange) {
            restRating -= maxRatingInThisRange;
        } else {
            return Math.ceil((restRating / finalExamScoreRate.rate) * 100) + beforeUpperBound;
        }
    }
    throw new Error("`rating` is out of range.");
};

const calculateNecessaryFinalExamScores = (finalExamRank, idolParameters) => {
    const finalExamRankData = finalExamRanks[finalExamRank];
    const idolParameterSum = idolParameters.vocal + idolParameters.dance + idolParameters.visual + finalExamRankData.idolParameterIncrease * 3;
    const fixedRating = finalExamRankData.rating + Math.floor((idolParameterSum * 23) / 10);

    return IdolRanks.map((idolRank) => ({
        name: idolRank.name,
        necessaryScore: calculateNecessaryFinalExamScoreForSpecificRating(Math.max(idolRank.necessaryRating - fixedRating, 0)),
    }));
};

const FinalExamScoreCalculator = () => {
    const [vocal, setVocal] = useState(1);
    const [dance, setDance] = useState(1);
    const [visual, setVisual] = useState(1);
    const [results, setResults] = useState([]);

    const handleCalculate = () => {
        const idolParameters = {vocal: Number(vocal), dance: Number(dance), visual: Number(visual)};
        const necessaryScores = calculateNecessaryFinalExamScores("1", idolParameters);
        setResults(necessaryScores);
    };

    return (
        <Paper style={{padding: 20, maxWidth: 400, margin: 'auto', marginTop: 20}}>
            <Typography variant="h5" style={{marginBottom: 20}}>期末得分</Typography>
            <Box display="flex" gap={2} flexDirection="column">
                <TextField label="Vocal" type="number" value={vocal} onChange={(e) => setVocal(e.target.value)}/>
                <TextField label="Dance" type="number" value={dance} onChange={(e) => setDance(e.target.value)}/>
                <TextField label="Visual" type="number" value={visual} onChange={(e) => setVisual(e.target.value)}/>
                <Box display="flex" justifyContent={"center"} style={{marginTop: 20}}>
                    <Button variant="contained" onClick={handleCalculate}>
                        计算
                    </Button>
                </Box>
            </Box>
            {results.length > 0 && (
                <TableContainer component={Paper} style={{marginTop: 20}}>
                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableCell>等级</TableCell>
                                <TableCell align="right">所需得分</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {results.map((result) => (
                                <TableRow key={result.name}>
                                    <TableCell>{result.name}</TableCell>
                                    <TableCell align="right">{result.necessaryScore.toLocaleString()}</TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            )}
        </Paper>
    );
};

export default FinalExamScoreCalculator;
