import React, { useState, useEffect } from 'react';
import * as cheerio from 'cheerio';
import {
    Button,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    List,
    ListItem,
    ListItemText,
    Paper,
    Typography,
    MenuItem,
    Select,
    FormControl,
    InputLabel,
    Box,
    Card,
    CardContent,
    CardMedia,
    CardActionArea,
    IconButton,
    Snackbar,
    CircularProgress
} from '@mui/material';
import Masonry from '@mui/lab/Masonry';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import categories from '../data/categories.json';
import data from '../data/data.json';

const SupportCards = () => {
    const [open, setOpen] = useState(false);
    const [selectedCategory, setSelectedCategory] = useState(null);
    const [selectedTotsu, setSelectedTotsu] = useState('');
    const [filteredCards, setFilteredCards] = useState([]);
    const [cards, setCards] = useState([]);
    const [snackbarOpen, setSnackbarOpen] = useState(false);
    const [loading, setLoading] = useState(true);
    const [dialogOpen, setDialogOpen] = useState(false);
    const [selectedCard, setSelectedCard] = useState(null);

    useEffect(() => {
        fetch('https://corsproxy.io/?https://appmedia.jp/gkmas/77848364')
            .then((response) => {
                if (!response.ok) {
                    throw new Error(response.status + ':' + response.statusText);
                }
                return response.text();
            })
            .then((html) => {
                const $ = cheerio.load(html);
                const script = $('script').get().find((script) => {
                    return script.children && script.children[0] && script.children[0].data && script.children[0].data.includes('comment_list_arr');
                });
                if (script && script.children && script.children[0] && script.children[0].data) {
                    const scriptContent = script.children[0].data;
                    const jsonMatch = scriptContent.match(/comment_list_arr\s*=\s*(\{[\s\S]*?});/);
                    if (jsonMatch && jsonMatch[1]) {
                        const json = JSON.parse(jsonMatch[1]);
                        json.parent.forEach((card) => {
                            card.requirements = JSON.parse(card.requirements);
                            card.source = 'Appmedia';
                        });
                        setCards(json.parent);
                    } else {
                        console.error('JSON object not found in script content');
                    }
                } else {
                    console.error('Script tag with comment_list_arr not found or malformed');
                }
            })
            .catch((error) => {
                alert('数据获取失败，请稍后再试。' + error);
                console.error('Error fetching or parsing the HTML:', error);
            })
            .finally(() => {
                data.forEach((item) => {
                    const pidMatch = item.content.match(/([A-Z0-9]{8})/);
                    if (pidMatch) {
                        const pid = pidMatch[1];
                        const category = categories.find(category => item.content.includes(category.data_name));
                        if (category) {
                            const supportId = category.data_id;
                            const supportTotsuMatch = item.content.match(/(.)凸/);
                            let supportTotsu = '0';
                            if (!supportTotsuMatch || supportTotsuMatch[1] === '無' || supportTotsuMatch[1] === '0' || supportTotsuMatch[1] === '０' || supportTotsuMatch[1] === '零') {
                                supportTotsu = '0';
                            } else if (supportTotsuMatch[1] === '1' || supportTotsuMatch[1] === '１' || supportTotsuMatch[1] === '一') {
                                supportTotsu = '1';
                            } else if (supportTotsuMatch[1] === '2' || supportTotsuMatch[1] === '２' || supportTotsuMatch[1] === '二') {
                                supportTotsu = '2';
                            } else if (supportTotsuMatch[1] === '3' || supportTotsuMatch[1] === '３' || supportTotsuMatch[1] === '三') {
                                supportTotsu = '3';
                            } else if (supportTotsuMatch[1] === '完' || supportTotsuMatch[1] === '4' || supportTotsuMatch[1] === '４' || supportTotsuMatch[1] === '四') {
                                supportTotsu = '4';
                            }
                            const card = {
                                unique_id: item.id,
                                author_name: item.author.nickname,
                                imgs: item.attachments.map(attachment => attachment.url),
                                requirements: {
                                    user_code: pid,
                                    support_id: supportId,
                                    support_totsu: supportTotsu
                                },
                                comment: item.content,
                                create_date: formatDate(item.timestamp),
                                source: '官方Discord'
                            };
                            setCards(cards => [...cards, card]);
                        }
                    }
                });
                setLoading(false);
            });
    }, []);

    const formatDate = (dateString) => {
        const date = new Date(dateString);
        const year = date.getFullYear();
        const month = String(date.getMonth() + 1).padStart(2, '0');
        const day = String(date.getDate()).padStart(2, '0');
        const hours = String(date.getHours()).padStart(2, '0');
        const minutes = String(date.getMinutes()).padStart(2, '0');
        const seconds = String(date.getSeconds()).padStart(2, '0');
        return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
    };

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleCategorySelect = (category) => {
        setSelectedCategory(category);
        filterCards(category, selectedTotsu);
        setOpen(false);
    };

    const handleTotsuChange = (event) => {
        const totsu = event.target.value;
        setSelectedTotsu(totsu);
        if (selectedCategory) {
            filterCards(selectedCategory, totsu);
        }
    };

    const filterCards = (category, totsu) => {
        const filtered = cards.filter(card =>
            card.requirements.support_id === category.data_id &&
            (totsu === '' || card.requirements.support_totsu === totsu)
        );
        setFilteredCards(filtered);
    };

    const handleCopyPID = (pid) => {
        navigator.clipboard.writeText(pid).then(() => {
            console.log('PID copied to clipboard:', pid);
            setSnackbarOpen(true);
        }).catch((error) => {
            console.error('Failed to copy PID:', error);
        });
    };

    const handleSnackbarClose = () => {
        setSnackbarOpen(false);
    };

    const handleCardClick = (card) => {
        setSelectedCard(card);
        setDialogOpen(true);
    };

    const handleDialogClose = () => {
        setDialogOpen(false);
    };

    useEffect(() => {
        setFilteredCards(cards.sort((a, b) => new Date(b.create_date) - new Date(a.create_date)));
    }, [cards]);

    return (
        <div>
            <Box sx={{display: 'flex', gap: 2, alignItems: 'center', p: 2}}>
                <Button variant="outlined" onClick={handleClickOpen} sx={{height: '50px'}}>
                    选择支援卡
                </Button>
                <FormControl>
                    <InputLabel id="totsu-label">凸数</InputLabel>
                    <Select
                        labelId="totsu-label"
                        value={selectedTotsu}
                        onChange={handleTotsuChange}
                        label="选择凸数"
                        sx={{height: '50px', lineHeight: '50px', minWidth: '100px'}}
                    >
                        <MenuItem value=""><em>全部</em></MenuItem>
                        <MenuItem value="1">1</MenuItem>
                        <MenuItem value="2">2</MenuItem>
                        <MenuItem value="3">3</MenuItem>
                        <MenuItem value="4">4</MenuItem>
                    </Select>
                </FormControl>
            </Box>
            <Dialog open={open} onClose={handleClose}>
                <DialogTitle>选择支援卡</DialogTitle>
                <DialogContent>
                    <List>
                        {categories.map((category) => (
                            <ListItem button onClick={() => handleCategorySelect(category)} key={category['data_id']}>
                                <Box sx={{display: 'flex', alignItems: 'center', gap: 2}}>
                                    <img src={category['src']} alt={category['data_name']}
                                         style={{width: '50px', height: '50px'}}/>
                                    <ListItemText primary={category['data_name']}/>
                                </Box>
                            </ListItem>
                        ))}
                    </List>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose} color="primary">
                        取消
                    </Button>
                </DialogActions>
            </Dialog>
            {loading ? (
                <Box sx={{display: 'flex', alignItems: 'center', p: 2}}>
                    <CircularProgress/>
                    <Typography variant="body2" color="textSecondary" sx={{ml: 2}}>
                        数据加载中...
                    </Typography>
                </Box>
            ) : !selectedCategory ? (
                <Typography variant="h6" component="div" sx={{p: 2}}>
                    请先选择支援卡
                </Typography>
            ) : (
                <Paper>
                    <Box sx={{display: 'flex', gap: 2, alignItems: 'center', p: 2}}>
                        <img src={selectedCategory['src']} alt={selectedCategory['data_name']}
                             style={{width: '50px', height: '50px'}}/>
                        <Typography variant="h6" component="div">
                            {selectedCategory['data_name']}
                        </Typography>
                    </Box>
                    <Typography variant="body2" color="textSecondary" sx={{p: 2}}>
                        共{filteredCards.length}张支援卡
                    </Typography>
                    <Masonry columns={{xs: 1, sm: 2, md: 3, lg: 4}} spacing={2}>
                        {filteredCards.map((card) => (
                            <Card key={card.unique_id} sx={{maxWidth: 345, position: 'relative'}}
                                  onClick={() => handleCardClick(card)}>
                                <CardActionArea>
                                    {card.img && (
                                        <CardMedia
                                            component="img"
                                            height="140"
                                            image={`https://article.matomeroyal.com/${card.img}`}
                                            alt={card.author_name}
                                        />
                                    )}
                                    {
                                        card.imgs && card.imgs.length > 0 && (
                                            <CardMedia
                                                component="img"
                                                height="140"
                                                image={card.imgs[0]}
                                                alt={card.author_name}
                                            />
                                        )
                                    }
                                    <CardContent>
                                        <Typography gutterBottom variant="h5" component="div">
                                            {card.author_name ? card.author_name : '匿名'}
                                        </Typography>
                                        <Typography variant="body1" color="textPrimary">
                                            凸数：{card.requirements.support_totsu}
                                        </Typography>
                                        <Typography variant="body2" color="textSecondary">
                                            {card.comment}
                                        </Typography>
                                        <Typography variant="body2" color="textSecondary">
                                            {card.create_date}
                                        </Typography>
                                        <Typography variant="body2" color="textSecondary">
                                            来源：{card.source}
                                        </Typography>
                                    </CardContent>
                                </CardActionArea>
                                <IconButton
                                    sx={{position: 'absolute', top: 8, right: 8}}
                                    onClick={() => handleCopyPID(card.requirements.user_code)}
                                >
                                    <ContentCopyIcon/>
                                </IconButton>
                            </Card>
                        ))}
                    </Masonry>
                </Paper>
            )}
            <Snackbar
                open={snackbarOpen}
                autoHideDuration={3000}
                onClose={handleSnackbarClose}
                message="PID已复制"
            />
            <Dialog open={dialogOpen} onClose={handleDialogClose}>
                <DialogTitle>
                    卡片详情
                    <IconButton
                        sx={{position: 'absolute', top: 8, right: 8}}
                        onClick={() => handleCopyPID(selectedCard.requirements.user_code)}
                    >
                        <ContentCopyIcon/>
                    </IconButton>
                </DialogTitle>
                <DialogContent>
                    {selectedCard && (
                        <Box>
                            <Typography variant="body2"
                                        color="textSecondary">PID：{selectedCard.requirements.user_code}</Typography>
                            <Typography variant="body2"
                                        color="textSecondary">卡名：{categories.find(category => category.data_id === selectedCard.requirements.support_id).data_name}</Typography>
                            <Typography variant="body2"
                                        color="textSecondary">昵称：{selectedCard.author_name ? selectedCard.author_name : '匿名'}</Typography>
                            <Typography variant="body2"
                                        color="textSecondary">凸数：{selectedCard.requirements.support_totsu}</Typography>
                            <Typography variant="body2" color="textSecondary">来源：{selectedCard.source}</Typography>
                            {selectedCard.comment && (
                                <Typography variant="body2"
                                            color="textSecondary">评论：{selectedCard.comment}</Typography>
                            )}
                            <Typography variant="body2"
                                        color="textSecondary">时间：{selectedCard.create_date}</Typography>
                            {selectedCard.imgs && selectedCard.imgs.length > 0 &&
                                selectedCard.imgs.map((img, index) => (
                                    <img key={index} src={img} alt={selectedCard.author_name}
                                         style={{width: '100%', marginTop: '10px'}}/>
                                ))
                            }
                            {selectedCard.img && (
                                <img src={`https://article.matomeroyal.com/${selectedCard.img}`}
                                     alt={selectedCard.author_name} style={{width: '100%', marginTop: '10px'}}/>
                            )}
                        </Box>
                    )}
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleDialogClose} color="primary">
                        关闭
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
};

export default SupportCards;
