import React, {useEffect} from 'react';
import CanvasNest from 'canvas-nest.js';

const CanvasNestComponent = () => {
    useEffect(() => {
        const cn = new CanvasNest(document.getElementById('canvas-nest'), {
            color: '0,0,0',  // 黑色
            opacity: 0.5,
            zIndex: -2,
            count: 99, // 粒子数量
        });

        return () => {
            cn.destroy(); // 清理
        };
    }, []);

    return (
        <div id="canvas-nest" style={{
            position: 'absolute',
            top: 0,
            left: 0,
            width: '100%',
            height: '100%',
            pointerEvents: 'none'  // 防止鼠标事件穿透
        }}/>
    );
};

export default CanvasNestComponent;
