import React from 'react';
import {Box, Typography} from "@mui/material";

export const Attribute = ({attribute, size='small'}) => {
    return (
        size === 'small' ?
        <Box display="flex" alignItems="center" gap={0.5}>
            <img src={`${process.env.PUBLIC_URL}/${attribute}.png`} alt={attribute}
                 style={{width: 12, height: 12}}/>
            <Typography variant="body2">{attribute}</Typography>
        </Box> :
        <Box display="flex" alignItems="center" gap={1}>
            <img src={`${process.env.PUBLIC_URL}/${attribute}.png`} alt={attribute}
                 style={{width: 16, height: 16}}/>
            <Typography variant="body1"><strong>{attribute}</strong></Typography>
        </Box>
    )
}
