import React, {useState} from 'react';
import {
    Box,
    Button,
    TextField,
    Typography,
    Paper,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
} from '@mui/material';
import {Attribute} from "../components/Attribute";

const SPAnalysis = () => {
    const [spValues, setSpValues] = useState({vocal: 0, dance: 0, visual: 0});
    const [analysisResults, setAnalysisResults] = useState([]);
    const [fourClassesProb, setFourClassesProb] = useState(null);

    const handleInputChange = (e) => {
        const {name, value} = e.target;
        setSpValues((prevValues) => ({
            ...prevValues,
            [name]: value,
        }));
    };

    const handleAnalyze = () => {
        const vocalProb = spValues.vocal / 100;
        const danceProb = spValues.dance / 100;
        const visualProb = spValues.visual / 100;

        const probabilities = {
            vocal: vocalProb,
            dance: danceProb,
            visual: visualProb,
            none: 1 - (vocalProb + danceProb + visualProb)
        };

        const combinations = [
            {'vocal': 4, 'dance': 0, 'visual': 0, 'none': 0},
            {'vocal': 3, 'dance': 1, 'visual': 0, 'none': 0},
            {'vocal': 3, 'dance': 0, 'visual': 1, 'none': 0},
            {'vocal': 3, 'dance': 0, 'visual': 0, 'none': 1},
            {'vocal': 2, 'dance': 2, 'visual': 0, 'none': 0},
            {'vocal': 2, 'dance': 1, 'visual': 1, 'none': 0},
            {'vocal': 2, 'dance': 1, 'visual': 0, 'none': 1},
            {'vocal': 2, 'dance': 0, 'visual': 2, 'none': 0},
            {'vocal': 2, 'dance': 0, 'visual': 1, 'none': 1},
            {'vocal': 2, 'dance': 0, 'visual': 0, 'none': 2},
            {'vocal': 1, 'dance': 3, 'visual': 0, 'none': 0},
            {'vocal': 1, 'dance': 2, 'visual': 1, 'none': 0},
            {'vocal': 1, 'dance': 2, 'visual': 0, 'none': 1},
            {'vocal': 1, 'dance': 1, 'visual': 2, 'none': 0},
            {'vocal': 1, 'dance': 1, 'visual': 1, 'none': 1},
            {'vocal': 1, 'dance': 1, 'visual': 0, 'none': 2},
            {'vocal': 1, 'dance': 0, 'visual': 3, 'none': 0},
            {'vocal': 1, 'dance': 0, 'visual': 2, 'none': 1},
            {'vocal': 1, 'dance': 0, 'visual': 1, 'none': 2},
            {'vocal': 1, 'dance': 0, 'visual': 0, 'none': 3},
            {'vocal': 0, 'dance': 4, 'visual': 0, 'none': 0},
            {'vocal': 0, 'dance': 3, 'visual': 1, 'none': 0},
            {'vocal': 0, 'dance': 3, 'visual': 0, 'none': 1},
            {'vocal': 0, 'dance': 2, 'visual': 2, 'none': 0},
            {'vocal': 0, 'dance': 2, 'visual': 1, 'none': 1},
            {'vocal': 0, 'dance': 2, 'visual': 0, 'none': 2},
            {'vocal': 0, 'dance': 1, 'visual': 3, 'none': 0},
            {'vocal': 0, 'dance': 1, 'visual': 2, 'none': 1},
            {'vocal': 0, 'dance': 1, 'visual': 1, 'none': 2},
            {'vocal': 0, 'dance': 1, 'visual': 0, 'none': 3},
            {'vocal': 0, 'dance': 0, 'visual': 4, 'none': 0},
            {'vocal': 0, 'dance': 0, 'visual': 3, 'none': 1},
            {'vocal': 0, 'dance': 0, 'visual': 2, 'none': 2},
            {'vocal': 0, 'dance': 0, 'visual': 1, 'none': 3},
            {'vocal': 0, 'dance': 0, 'visual': 0, 'none': 4},
        ];

        // 计算阶乘
        const factorial = (n) => {
            if (n <= 1) return 1;
            return n * factorial(n - 1);
        };

        // 计算每个情况的概率
        const results = combinations.map((combination) => {
            const total = Object.values(combination).reduce((sum, count) => sum + count, 0);
            const arrangementCount = factorial(total) /
                Object.values(combination).reduce((acc, count) => acc * factorial(count), 1);

            const probability = Object.keys(combination).reduce((acc, key) => {
                return acc * Math.pow(probabilities[key], combination[key]);
            }, 1);

            return {combination, probability: probability * arrangementCount};
        });

        // 排序并去掉概率为0的情况
        const filteredResults = results.filter((result) => result.probability > 0);
        filteredResults.sort((a, b) => b.probability - a.probability);
        setAnalysisResults(filteredResults);

        const singleClassProb = 1 - (1 - vocalProb) * (1 - danceProb) * (1 - visualProb);
        const fourClassesProb = Math.pow(singleClassProb, 4);
        setFourClassesProb(fourClassesProb);
    };

    return (
        <Paper style={{padding: 20, maxWidth: 400, margin: 'auto', marginTop: 20}}>
            <Typography variant="h5" style={{marginBottom: 20}}>SP分析</Typography>
            <Box display="flex" flexDirection="column" gap={2}>
                <TextField
                    label="Vocal"
                    name="vocal"
                    type="number"
                    value={spValues.vocal}
                    onChange={handleInputChange}
                />
                <TextField
                    label="Dance"
                    name="dance"
                    type="number"
                    value={spValues.dance}
                    onChange={handleInputChange}
                />
                <TextField
                    label="Visual"
                    name="visual"
                    type="number"
                    value={spValues.visual}
                    onChange={handleInputChange}
                />
                <Box display="flex" justifyContent={"center"} style={{marginTop: 20}}>
                    <Button variant="contained" onClick={handleAnalyze}>
                        计算
                    </Button>
                </Box>

                {fourClassesProb !== null && (
                    <Box style={{marginTop: 20}}>
                        <Typography>
                            掉SP率: {(100 - fourClassesProb * 100).toFixed(2)}%
                        </Typography>
                        <Typography>
                            满SP期望次数: {(1 / fourClassesProb).toFixed(2)}
                        </Typography>
                    </Box>
                )}

                {analysisResults.length > 0 && (
                    <TableContainer component={Paper} style={{marginTop: 20}}>
                        <Table>
                            <TableHead>
                                <TableRow>
                                    <TableCell><Attribute attribute="Vocal"/></TableCell>
                                    <TableCell><Attribute attribute="Dance"/></TableCell>
                                    <TableCell><Attribute attribute="Visual"/></TableCell>
                                    <TableCell><Typography variant="body2">无</Typography></TableCell>
                                    <TableCell align="right"><Typography variant="body2">概率</Typography></TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {analysisResults.map((result, index) => (
                                    <TableRow key={index}>
                                        <TableCell>{result.combination.vocal}</TableCell>
                                        <TableCell>{result.combination.dance}</TableCell>
                                        <TableCell>{result.combination.visual}</TableCell>
                                        <TableCell>{result.combination.none}</TableCell>
                                        <TableCell align="right">{(result.probability * 100).toFixed(2)}%</TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </TableContainer>
                )}
            </Box>
        </Paper>
    );
};

export default SPAnalysis;
