import React, { useState } from 'react';
import { AppBar, Tabs, Tab, Typography, Box, createTheme, ThemeProvider } from '@mui/material';
import FinalExamScoreCalculator from './pages/FinalExamScoreCalculator';
import TrainingScoreCalculator from './pages/TrainingScoreCalculator';
import SPAnalysis from './pages/SPAnalysis';
import SupportCards from "./pages/SupportCards";
import CanvasNestComponent from "./components/CanvasNestComponent";
import RecommendedStrategy from "./pages/RecommendedStrategy";

const theme = createTheme({
    components: {
        MuiTabs: {
            styleOverrides: {
                root: {
                    backgroundColor: '#3f51b5',
                },
                indicator: {
                    backgroundColor: '#ffeb3b',
                },
            },
        },
        MuiTab: {
            styleOverrides: {
                root: {
                    color: '#ffffff',
                    '&.Mui-selected': {
                        color: '#ffeb3b',
                    },
                },
            },
        },
    },
});

const TabPanel = (props) => {
    const { children, value, index, ...other } = props;
    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`tabpanel-${index}`}
            aria-labelledby={`tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box sx={{ p: 3 }}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
};

const App = () => {
    const [value, setValue] = useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <ThemeProvider theme={theme}>
            <CanvasNestComponent />
            <AppBar position="static">
                <Tabs value={value} onChange={handleChange} centered>
                    <Tab label="期末得分" />
                    <Tab label="追加训练" />
                    <Tab label="SP分析" />
                    <Tab label="借支援卡" />
                    {/*<Tab label="推荐编组" />*/}
                </Tabs>
            </AppBar>
            <TabPanel value={value} index={0}>
                <FinalExamScoreCalculator />
            </TabPanel>
            <TabPanel value={value} index={1}>
                <TrainingScoreCalculator />
            </TabPanel>
            <TabPanel value={value} index={2}>
                <SPAnalysis />
            </TabPanel>
            <TabPanel value={value} index={3}>
                <SupportCards />
            </TabPanel>
            {/*<TabPanel value={value} index={4}>*/}
            {/*    <RecommendedStrategy />*/}
            {/*</TabPanel>*/}
        </ThemeProvider>
    );
};

export default App;
